<template>
  <div class="portal">
    <nav id="main-nav">
      <ul class="m">
        <li @click="getarticle(0)" :class="active===0?'active':''"
            >
          热门
        </li>
        <li @click="tochange(index+1,item.bid)" :class="active===index+1?'active':''" v-for="(item,index) in tabs"
            :key="index">
          {{item.name}}
        </li>
      </ul>
    </nav>
    <div class="main m">
      <div class="main-left">
        <articles :article="article" v-on:islike="islike" v-on:isrubbish="isrubbish"></articles>
      </div>
      <div class="main-right">
      <div class="user" style="margin-bottom: 20px;">
        <userinfo></userinfo>
      </div>
        <recommend></recommend>
        <answer></answer>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "portal",
    data() {
      return {
        //输入框搜索
        restaurants: [],
        search: '',
        timeout: null,
        tabs: [],
        active: 0,
        bid: '',
        article: [],
        userInfo: {},
        recom: [],
        dialog: false,
        dialog1: true,
        dialog2: false,
        querySearch: '',
        textarea: ''
      };
    },
    mounted() {
      this.getdata();
      this.getarticle(0);
      // setTimeout(() => {
      //   this.getArt(this.bid)
      // }, 600)
    },
    methods: {
      //点赞后
      islike(data) {
        this.getArt(this.bid)
      },
      isrubbish(data){
        this.getArt(this.bid)
      },
      //获取tab栏数据
      getdata() {
        this.$post("index/data/question_type").then(res => {
          this.bid = res.data[0].bid;
          this.tabs = res.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //获取文章数据
      getarticle(index) {
        this.active = index;
        this.$post("index/data/question_answer").then(res => {
          this.article = res.data;
          this.article.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.article = this.article.slice();
        }).catch(err => {
          console.log(err);
        })
      },
      //切换文章数据
      getArt(bid) {
        // this.bid = bid;
        this.$post("index/data/label_question", {bid}).then(res => {
          this.article = res.data.data;
          this.article.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.article = this.article.slice();
        }).catch(err => {
          console.log(err);
        })
      },
      //tab切换
      tochange(index, bid) {
        this.active = index;
        this.getArt(bid);
      },
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;

  #main-nav {
    position: fixed;
    top: 57px;
    z-index: 1000;
    left: 0;
    width: 100%;
    line-height: 44px;
    height: 44px;
    color: rgba(51, 51, 51, 1);
    font-weight: bold;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.12);

    ul {
      display: flex;
      justify-content: flex-start;
      height: 100%;

      li {
        height: 100%;
        width: 80px;
        position: relative;
        text-align: center;
        margin-right: 30px;
        cursor: pointer;
        user-select: none;
        font-weight: bold;
        font-size: 18px;

      }

      .active {
        color: $main;

        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          display: inline-block;
          width: 100%;
          height: 4px;
          border-radius: 2px;
          background-color: $main;
        }
      }
    }
  }

  .main {
    margin-top: 80px;
    display: flex;
    justify-content: space-between;

    .main-left {
      width: 788px;
    }

    .main-right {
      width: 350px;
    }
  }

</style>



